'use client';

import Link from 'next/link';
import { CheckCircle } from 'lucide-react';
import { useState, useEffect } from 'react';

interface PricingDataItem {
  id: string;
  name: string;
  description: string;
  price: string;
  priceId: string | null;
  features: string[];
}

export default function LandingPage() {
  const [pricingData, setPricingData] = useState<PricingDataItem[]>([]);
  const [loadingPricing, setLoadingPricing] = useState(true);
  const [errorPricing, setErrorPricing] = useState<string | null>(null);

  useEffect(() => {
    const fetchPricing = async () => {
      try {
        const response = await fetch('/api/pricing');
        if (!response.ok) {
          throw new Error('Failed to fetch pricing data');
        }
        const data = await response.json();
        setPricingData(data.pricingData);
      } catch (err) {
        setErrorPricing(err instanceof Error ? err.message : 'An unknown error occurred');
      } finally {
        setLoadingPricing(false);
      }
    };

    fetchPricing();
  }, []);

  return (
    <div className="min-h-screen bg-gradient-to-b from-white to-gray-50">
      {/* Hero Section */}
      <div className="container mx-auto px-4 py-20">
        <div className="text-center max-w-4xl mx-auto">
          <h1 className="text-5xl font-bold mb-6 bg-clip-text text-transparent bg-gradient-to-r from-blue-600 to-purple-600">
            Make Your Website Accessible to Everyone
          </h1>
          <p className="text-xl text-gray-600 mb-8">
            Automated accessibility testing and compliance monitoring for WCAG, ADA, and AODA standards.
            Get detailed reports and step-by-step fixes for your website.
          </p>
          <div className="flex gap-4 justify-center">
            <Link 
              href="/dashboard" 
              className="bg-blue-600 text-white px-8 py-3 rounded-lg font-semibold hover:bg-blue-700 transition"
            >
              Get Started
            </Link>
            <Link 
              href="#pricing" 
              className="bg-white text-blue-600 px-8 py-3 rounded-lg font-semibold border border-blue-600 hover:bg-blue-50 transition"
            >
              View Pricing
            </Link>
          </div>
        </div>
      </div>

      {/* Features Section */}
      <div className="bg-white py-20">
        <div className="container mx-auto px-4">
          <h2 className="text-3xl font-bold text-center mb-12">Comprehensive Accessibility Testing</h2>
          <div className="grid md:grid-cols-3 gap-8">
            <FeatureCard 
              title="WCAG 2.0 Compliance"
              description="Full coverage of WCAG 2.0 Level A and AA requirements for comprehensive accessibility testing."
            />
            <FeatureCard 
              title="ADA & AODA Standards"
              description="Ensure compliance with ADA and AODA regulations through automated testing and reporting."
            />
            <FeatureCard 
              title="Step-by-Step Fixes"
              description="Get detailed instructions and code examples to fix accessibility issues quickly and effectively."
            />
          </div>
        </div>
      </div>

      {/* Pricing Section */}
      <div id="pricing" className="py-20 bg-gray-50">
        <div className="container mx-auto px-4">
          <h2 className="text-3xl font-bold text-center mb-12">Simple, Transparent Pricing</h2>
          {loadingPricing ? (
            <div className="text-center py-8">
              <p className="text-gray-600">Loading pricing plans...</p>
            </div>
          ) : errorPricing ? (
            <div className="text-center py-8 text-red-600">
              <p>Error: {errorPricing}</p>
              <p>Please check your Stripe configuration.</p>
            </div>
          ) : (
            <div className="grid md:grid-cols-3 gap-8 max-w-6xl mx-auto">
              {pricingData.map((plan) => (
                <PricingCard
                  key={plan.id}
                  title={plan.name}
                  price={plan.price !== 'Contact Sales' ? `$${plan.price}` : plan.price}
                  period={plan.price !== 'Contact Sales' ? 'month' : ''}
                  features={plan.features}
                  buttonText={plan.price === 'Contact Sales' ? 'Contact Sales' : 'Start Free Trial'}
                  highlighted={plan.name === 'Pro'}
                  priceId={plan.priceId}
                />
              ))}
            </div>
          )}
        </div>
      </div>

      {/* CTA Section */}
      <div className="bg-blue-600 text-white py-20">
        <div className="container mx-auto px-4 text-center">
          <h2 className="text-3xl font-bold mb-6">Ready to Make Your Website Accessible?</h2>
          <p className="text-xl mb-8 opacity-90">
            Join thousands of websites that are now accessible to everyone.
          </p>
          <Link 
            href="/dashboard" 
            className="bg-white text-blue-600 px-8 py-3 rounded-lg font-semibold hover:bg-gray-100 transition inline-block"
          >
            Start Your Free Trial
          </Link>
        </div>
      </div>
    </div>
  );
}

function FeatureCard({ title, description }: { title: string; description: string }) {
  return (
    <div className="bg-white p-6 rounded-xl shadow-sm border border-gray-100">
      <h3 className="text-xl font-semibold mb-3">{title}</h3>
      <p className="text-gray-600">{description}</p>
    </div>
  );
}

interface PricingCardProps {
  title: string;
  price: string;
  period: string;
  features: string[];
  buttonText: string;
  highlighted: boolean;
  priceId: string | null;
}

function PricingCard({ 
  title, 
  price, 
  period, 
  features, 
  buttonText, 
  highlighted,
  priceId
}: PricingCardProps) {
  const handleButtonClick = async () => {
    if (buttonText === "Contact Sales") {
      window.location.href = "/contact";
    } else if (priceId) {
      try {
        const response = await fetch('/api/create-checkout-session', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ priceId }),
        });

        const session = await response.json();
        if (session.url) {
          window.location.href = session.url;
        } else {
          console.error('Failed to create checkout session', session.error);
          alert('Failed to start trial. Please try again.');
        }
      } catch (error) {
        console.error('Error creating checkout session:', error);
        alert('An error occurred. Please try again.');
      }
    } else {
      window.location.href = "/dashboard";
    }
  };

  return (
    <div className={`bg-white rounded-xl shadow-sm border ${highlighted ? 'border-blue-600' : 'border-gray-100'} p-8 relative`}>
      {highlighted && (
        <div className="absolute -top-4 left-1/2 transform -translate-x-1/2">
          <span className="bg-blue-600 text-white px-4 py-1 rounded-full text-sm font-semibold">
            Most Popular
          </span>
        </div>
      )}
      <h3 className="text-2xl font-bold mb-2">{title}</h3>
      <div className="mb-6">
        <span className="text-4xl font-bold">{price}</span>
        {period && <span className="text-gray-600">/{period}</span>}
      </div>
      <ul className="space-y-4 mb-8">
        {features.map((feature, index) => (
          <li key={index} className="flex items-center">
            <CheckCircle className="w-5 h-5 text-blue-600 mr-2" />
            <span>{feature}</span>
          </li>
        ))}
      </ul>
      <button
        onClick={handleButtonClick}
        className={`block text-center py-3 rounded-lg font-semibold transition ${
          highlighted
            ? 'bg-blue-600 text-white hover:bg-blue-700'
            : 'bg-gray-100 text-gray-800 hover:bg-gray-200'
        }`}
      >
        {buttonText}
      </button>
    </div>
  );
}
